import React from 'react';
import { createBrowserRouter } from 'react-router-dom';
import { lazy, Suspense } from 'react';
import workspaceRouter from '@router/workspaceRouter';
import memberRouter from '@router/memberRouter';
import PrivateRoute from '@components/PrivateRoute';
import ErrorBoundary from '@components/ErrorBoundary';

const Loading = (
  <div
    style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh', background: '#F4F4F4' }}
  >
    <p>Loading...</p>
  </div>
);

const BasicLayout = lazy(() => import('@layouts/BasicLayout'));
const WorkspaceIndex = lazy(() => import('@pages/workspace'));
const MemberIndex = lazy(() => import('@pages/member'));
const NotFoundPage = lazy(() => import('@pages/NotFoundPage'));
const RootRedirectPage = lazy(() => import('@pages/RootRedirectPage'));

const root = createBrowserRouter(
  [
    {
      path: '/',
      element: (
        <ErrorBoundary>
          <Suspense fallback={Loading}>
            <BasicLayout />
          </Suspense>
        </ErrorBoundary>
      ),
      children: [
        {
          path: '',
          element: <RootRedirectPage />,
        },
        {
          path: 'workspace/:url',
          element: (
            <PrivateRoute>
              <Suspense fallback={Loading}>
                <WorkspaceIndex />
              </Suspense>
            </PrivateRoute>
          ),
          children: workspaceRouter(),
        },
        {
          path: 'member',
          element: (
            <Suspense fallback={Loading}>
              <MemberIndex />
            </Suspense>
          ),
          children: memberRouter(),
        },
        {
          path: '*', // 모든 경로가 처리되지 않을 경우
          element: (
            <Suspense fallback={Loading}>
              <NotFoundPage />
            </Suspense>
          ),
        },
      ],
    },
  ],
  { basename: '/' },
);

export default root;
